* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  min-height: 100%;
  min-width: 600px;
}

body {
  font-family: sans-serif;
  background: linear-gradient(#ff000033, rgba(0, 0, 255, 0.2));
  background-repeat: no-repeat;
}
#root {
  width: 100%;
}

@media screen and (max-width: 960px) {
  .App {
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    height: 100%;
    padding: 2rem;
    justify-content: space-evenly;
    gap: 20px;
  }
}

.App {
  width: 100%;
  height: 100%;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-evenly;
}
